getContextMenu()

function getContextMenu() {
    events.forEach((eventType) => {
        document.addEventListener(
            eventType,
            (e) => {
                e.preventDefault()

                let mouseX = e.clientX || e.touches[0].clientX
                let mouseY = e.clientY || e.touches[0].clientY

                let menuHeight = contextMenu.getBoundingClientRect().height
                let menuWidth = contextMenu.getBoundingClientRect().width

                let width = window.innerWidth
                let height = window.innerHeight

                if (width - mouseX <= 200) {
                    contextMenu.style.left = width - menuWidth + 'px'
                    contextMenu.style.top = mouseY + 'px'

                    if (height - mouseY <= 200) {
                        contextMenu.style.top = mouseY - menuHeight + 'px'
                    }
                } else {
                    contextMenu.style.left = mouseX + 'px'
                    contextMenu.style.top = mouseY + 'px'

                    if (height - mouseY <= 200) {
                        contextMenu.style.top = mouseY - menuHeight + 'px'
                    }
                }

                contextMenu.style.visibility = 'visible'
            },
            {
                passive: false,
            }
        )
    })

    document.addEventListener('touchend', function (e) {
        var currentTime = new Date().getTime()
        var tapLength = currentTime - lastTap

        clearTimeout(timeout)

        if (tapLength < 500 && tapLength > 0) {
            contextMenu.style.visibility = 'hidden'
            e.preventDefault()
        } else {
            timeout = setTimeout(function () {
                clearTimeout(timeout)
            }, 500)
        }
        lastTap = currentTime
    })

    document.addEventListener('click', function (e) {
        if (!contextMenu.contains(e.target)) {
            contextMenu.style.visibility = 'hidden'
        }
    })
}

getTimeDate()

function getTimeDate() {
    //time - date
    function zero_first_format(value) {
        if (value < 10) {
            value = '0' + value
        }
        return value
    }

    function time() {
        var current_datetime = new Date()
        var hours = zero_first_format(current_datetime.getHours())
        var minutes = zero_first_format(current_datetime.getMinutes())

        return hours + ':' + minutes
    }

    setInterval(function () {
        document.getElementById('time').innerHTML = time()
        document.getElementById('time-2').innerHTML = time()
        document.getElementById('time-3').innerHTML = time()
    }, 1000)

    function date() {
        var current_datetime = new Date()
        var day = zero_first_format(current_datetime.getDate())
        var month = zero_first_format(current_datetime.getMonth() + 1)
        var year = current_datetime.getFullYear()

        return day + '.' + month + '.' + year
    }

    setInterval(function () {
        document.getElementById('date').innerHTML = date()
        document.getElementById('date-2').innerHTML = date()
        document.getElementById('date-3').innerHTML = date()
    }, 1000)
}

getLockScreen()

function getLockScreen() {
    function getSubmitPasswordCheck() {
        if (password.value == 'admin6&33') {
            classLockSreen.classList.add(addClassDisplayNone)
            classActivePC.style.opacity = '1'
        } else {
            classInputValid.classList.add(addClassWrongPassword)
        }
    }

    btnCheck.addEventListener('click', function () {
        getSubmitPasswordCheck()
    })

    document.addEventListener('keydown', function (enter) {
        if (enter.keyCode == 13) {
            getSubmitPasswordCheck()
        }
    })

    timerId = setTimeout(function tick() {
        classInputValid.classList.remove(addClassWrongPassword)

        let checkLockscreen = document.querySelector('.noactive-lockscreen')
        if (checkLockscreen) {
            document.querySelector('#remove').innerHTML = ''
        }
        timerId = setTimeout(tick, 1000)
    }, 1000)
}

