getPlayerMusic()

function getPlayerMusic() {
    document.addEventListener('DOMContentLoaded', (e) => {
        let av = document.getElementById('av-tag')
        let playTime = document.getElementsByClassName('play-time')[0]
        let playBtn = document.getElementsByClassName('play-btn')[0]
        let curTime = document.getElementById('cur-time')
        let volume = document.getElementById('volume')
        let speaker = document.getElementById('speaker')

        let isPlaying = false

        av.onloadedmetadata = function () {
            curTime.max = av.duration
        }

        av.ontimeupdate = function () {
            var sec_num = av.currentTime
            var hours = Math.floor(sec_num / 3600)
            var minutes = Math.floor((sec_num - hours * 3600) / 60)
            var seconds = sec_num - hours * 3600 - minutes * 60
            seconds = Math.round(seconds)

            if (hours < 10) {
                hours = '0' + hours
            }
            if (minutes < 10) {
                minutes = '0' + minutes
            }
            if (seconds < 10) {
                seconds = '0' + seconds
            }
            playTime.innerHTML = minutes + ':' + seconds
            if (isPlaying) curTime.value = av.currentTime
        }

        volume.onchange = function () {
            av.volume = volume.value / 10
        }

        curTime.onchange = function () {
            av.pause()
            av.currentTime = curTime.value
            av.play()
        }

        speaker.onclick = function () {
            if (volume.value == 0) {
                volume.value = 10
                av.volume = 1
            } else {
                volume.value = 0
                av.volume = 0
            }
        }

        playBtn.addEventListener('click', (a) => {
            if (isPlaying) {
                av.pause()
                isPlaying = false
                playBtn.innerHTML =
                    '<svg><use xlink:href="img/sprite.svg#icon-play"></use></svg>'
            } else {
                av.play()
                isPlaying = true
                playBtn.innerHTML =
                    '<svg><use xlink:href="img/sprite.svg#icon-pause"></use></svg>'
            }
        })
    })
}

