getAreaSelection()

function getAreaSelection() {
    document.addEventListener('mousedown', (event) => {
        if (event.target.classList.contains('area-selection')) {
            startX = event.clientX
            startY = event.clientY

            selectionRectangle = document.createElement('div')
            selectionRectangle.className = 'selection-rectangle'
            selectionRectangle.style.left = startX + 'px'
            selectionRectangle.style.top = startY + 'px'
            document.body.appendChild(selectionRectangle)
        }
    })

    document.addEventListener('mousemove', (event) => {
        if (selectionRectangle) {
            endX = event.clientX
            endY = event.clientY

            let width = Math.abs(endX - startX)
            let height = Math.abs(endY - startY)
            let left = Math.min(startX, endX)
            let top = Math.min(startY, endY)

            selectionRectangle.style.width = width + 'px'
            selectionRectangle.style.height = height + 'px'
            selectionRectangle.style.left = left + 'px'
            selectionRectangle.style.top = top + 'px'

            let selectedElements = document.querySelectorAll(
                '.block-folder, .block-table, .block-doc, .block-slides, .block-info, .block-donate'
            )
            selectedElements.forEach((element) => {
                let elementRect = element.getBoundingClientRect()
                if (
                    elementRect.left < left + width &&
                    elementRect.left + elementRect.width > left &&
                    elementRect.top < top + height &&
                    elementRect.top + elementRect.height > top
                ) {
                    element.classList.add('block-area-active')
                } else {
                    element.classList.remove('block-area-active')
                }
            })
        }
    })

    document.addEventListener('mouseup', (event) => {
        if (selectionRectangle) {
            document.body.removeChild(selectionRectangle)
            selectionRectangle = null
        }
    })
}

getBlockAreaFolder()

function getBlockAreaFolder() {
    let isDragging = false
    let offset = { x: 0, y: 0 }

    blockFolder.addEventListener('mousedown', (e) => {
        if (e.button === 0) {
            // left mouse button
            isDragging = true
            offset.x = e.pageX - blockFolder.offsetLeft
            offset.y = e.pageY - blockFolder.offsetTop
        }
    })

    document.addEventListener('mouseup', (e) => {
        if (e.button === 0) {
            // left mouse button
            isDragging = false
        }
    })

    document.addEventListener('mousemove', (e) => {
        if (isDragging) {
            const maxX = window.innerWidth - blockFolder.offsetWidth
            const maxY = window.innerHeight - blockFolder.offsetHeight
            let x = e.pageX - offset.x
            let y = e.pageY - offset.y
            x = Math.max(0, Math.min(x, maxX))
            y = Math.max(0, Math.min(y, maxY))
            blockFolder.style.left = x + 'px'
            blockFolder.style.top = y + 'px'
        }
    })
}

