function getWindowActiveBtn() {
    btn1.addEventListener('click', function () {
        btn1.classList.add(addClassBtnNavFocus)
        btn1.classList.add(addClassBtnNavActive)
        classWindow1.classList.add(addClassWindowActive)

        if (btn2.classList.contains(addClassBtnNavActive)) {
            btn1.classList.remove(addClassBtnNavNoneFocus)
            btn2.classList.add(addClassBtnNavNoneFocus)
        }

        if (classWindow1.classList.contains(addClassWindowFold)) {
            btn1.classList.add(addClassBtnNavFocus)
            btn1.classList.remove(addClassBtnNavNoneFocus)
            classWindow1.classList.remove(addClassWindowFold)
        }
    })

    btn2.addEventListener('click', function () {
        btn2.classList.add(addClassBtnNavFocus)
        btn2.classList.add(addClassBtnNavActive)
        classWindow2.classList.add(addClassWindowActive)

        if (btn1.classList.contains(addClassBtnNavActive)) {
            btn2.classList.remove(addClassBtnNavNoneFocus)
            btn1.classList.add(addClassBtnNavNoneFocus)
        }

        if (classWindow1.classList.contains(addClassBtnWindow2Fold)) {
            btn2.classList.add(addClassBtnNavFocus)
            btn2.classList.remove(addClassBtnNavNoneFocus)
            classWindow1.classList.remove(addClassBtnWindow2Fold)
        }
    })

    classBtnWindow2Fold.addEventListener('click', function () {
        btn2.classList.add(addClassBtnNavNoneFocus)
        classWindow2.classList.remove(addClassWindowActive)
        classWindow2.classList.toggle(addClassBtnWindow1Fold)
    })

    classBtnWindow2Expand.addEventListener('click', function () {
        classWindow2.classList.toggle(addClassWindowExpand)
    })

    classBtnWindow2Close.addEventListener('click', function () {
        btn2.classList.remove(addClassBtnNavFocus)
        btn2.classList.remove(addClassBtnNavActive)
        btn2.classList.remove(addClassBtnNavNoneFocus)
        classWindow2.classList.remove(addClassWindowActive)
    })
}

getAccordionItems()

function getAccordionItems() {
    const accordionItems = document.querySelectorAll('.accordion-item')

    accordionItems.forEach((item) => {
        const header = item.querySelector('.accordion-header')
        const content = item.querySelector('.accordion-content')

        header.addEventListener('click', () => {
            item.classList.toggle('open')
        })
    })
}

getTheme()

function getTheme() {
    // Получаем кнопку и элемент, к которому нужно добавить класс
    const classActiveTheme = 'active-theme'
    const btnTheme = document.querySelector('.btn-theme')
    const bodyElement = document.querySelector('.body')
    const addClassBtnActiveModifi = 'modal-modifi-nav__item_active'

    // Проверяем, есть ли сохраненное состояние класса в localStorage
    if (localStorage.getItem('activeTheme') === 'true') {
        // Если есть, добавляем класс
        bodyElement.classList.add(classActiveTheme)
        btnTheme.classList.add(addClassBtnActiveModifi)
    }

    // Обработчик события при нажатии на кнопку
    btnTheme.addEventListener('click', function () {
        // Проверяем, есть ли класс у элемента
        if (bodyElement.classList.contains(classActiveTheme)) {
            // Если есть, удаляем класс и сохраняем состояние в localStorage
            bodyElement.classList.remove(classActiveTheme)
            btnTheme.classList.remove(addClassBtnActiveModifi)
            localStorage.setItem('activeTheme', 'false')
        } else {
            // Если нет, добавляем класс и сохраняем состояние в localStorage
            bodyElement.classList.add(classActiveTheme)
            btnTheme.classList.add(addClassBtnActiveModifi)
            localStorage.setItem('activeTheme', 'true')
        }
    })
}
